<template>
	<div class="wrap success_registre">
		<!-- 头部标题 -->
		<div class="header">
			<mt-header fixed title="完成注册">
		      <mt-button icon="back" slot="left" @click.prevent="goBack"></mt-button>
			  <mt-button icon="more" slot="right"></mt-button>
			</mt-header>
		</div>
		<!-- 头部标题 end -->
		<div class="con">
			<img src="../../../static/images/icon_sccess.png" alt="">
			<h2>恭喜您注册成功!</h2>
			<p>您已成为阿康会员，马上去选购商品吧！</p>
			<a 
				@click="jumpPage"
				href="javascript:;">确定</a>
			<span>{{ time }}秒后返回</span>
		</div>
	</div>
</template>

<script>
import { Header, Button, Toast } from 'mint-ui'; 
export default {
	name: 'SuccessRegistre',
	data (){
		return {
			time: 5,
		}
	},
	components: {
		'mt-header': Header,
		'mt-button': Button,
	},
	created (){
		const _self = this;
		var timer = null;

		clearInterval(timer);
		timer = setInterval(function(){
			_self.time--;
			if( _self.time < 0 ){
				clearInterval(timer);
				_self.time = 0;
				_self.$router.go(-1);
			}
		},1000);
	},
	methods: {
		//跳转页面
		jumpPage (){
			this.$router.go(-1);
		},
		//后退
		goBack (){
			this.$router.go(-1);
		},
	},
}
</script>

<style scoped>
@import './Css/SuccessRegistre.css';	
</style>


